<template lang="pug">
.tree-grid(:style="{height: TableAreaHeight + 'px'}")
  Spin(size="large", fix, v-if="loading")
  .tree-table
    .top-menu
      h1
        | 树形组件数据拓展 &nbsp;
        Button(type="info" @click="addAdditionalDataFilter")
          | 添加新时间段
        Button(type="warning" @click="sort()" style="margin-left: 10px")
          | 时间段排序
        //- Button(type="primary" @click="save()" style="margin-left: 10px")
        //-   | 全部保存
    .tabs-area(v-if="additionalDataFiliterList && additionalDataFiliterList.length")
      Spin(size="large" fix v-if="loadingData")
      Tabs(:animated="false" type="card" v-model="nowEditId")
        TabPane(v-for="(item, index) in additionalDataFiliterList" :key="index" :label="item.name" :name="item.id")
          .tool-show
            strong 有效期: &nbsp;
            span(v-show="item.type != 3")
              | {{ getDateTime(item.startTime) }} &nbsp; - &nbsp; {{ item.type == 2 ? '长期' : getDateTime(item.endTime) }}
            span(v-show="item.type == 3")
              | 永久
          .tool-menu
            Button(type="error" @click="handleTabRemove(item.id)" style="margin-right: 10px")
              | 删除此时间段
            Button(type="warning" @click="updateAdditionalDataFilter(item.id)" style="margin-right: 10px")
              | 修改此时间段设置
            Button(type="error" @click="handleColRemove(item.id)" style="margin-right: 10px")
              | 删除此列
            Button(type="warning" @click="editCol(item.id)" style="margin-right: 10px")
              | 修改此列
            Button(type="info" @click="addCol(item.id)" style="margin-right: 10px")
              | 添加新列
            Button(type="warning" @click="sortCol" style="margin-right: 10px")
              | 列排序
            Button(type="primary" @click="save(item.id, true)")
              | 保存
      #grid
    Alert(v-else type="warning")
      | 未查询到该组件的数据拓展信息，请先
      a(@click="addAdditionalDataFilter")
        | 添加时间段
      | 。
  Modal(v-model="showAdditionalDataFilterModel" :title="(filterTemplate.isEdit ? '编辑' : '添加新') + '时间段'" @on-ok="additionalDataFilterHander" :loading="showAdditionalDataFilterModelLoading")
    Form(:label-width="100" ref="filterFormValidate" :model="filterTemplate" :rules="filterValidateRule")
      FormItem(label="名称" prop="showName")
        Input(v-model="filterTemplate.showName")
      FormItem(label="自定义代码" prop="name")
        Input(v-model="filterTemplate.name")
      FormItem(label="有效期类型" prop="type")
        Select(v-model="filterTemplate.type" @on-change="filterTemplateType")
          Option(value="1") 起始时间 - 终止时间
          Option(value="2") 某个时间开始 - 长期
          Option(value="3") 永久
      FormItem(label="有效期", v-if="filterTemplate.type != 3")
        DatePicker(v-if="filterTemplate.type == 1" :value="filterTemplate.times" @on-change="filterTemplateDate" type="datetimerange" format="yyyy-MM-dd HH:mm:ss" placeholder="请选择起始时间和终止时间" style="width: 100%")
        div(v-if="filterTemplate.type == 2")
          DatePicker(type="datetime" placeholder="请选择起始日期" :value="getDateTime(filterTemplate.startTime)" format="yyyy-MM-dd HH:mm:ss" @on-change="filterTemplateDateLongTime")
          | &nbsp; - 长期
      FormItem(label="数据来源" v-if="!filterTemplate.isEdit")
        Select(v-model="filterTemplate.dataSourceId")
          Option(v-for="(item, index) in additionalDataFiliterList" :key="index" :value="item.id") {{ item.name }}
      FormItem(label="包含数据" v-if="!filterTemplate.isEdit && filterTemplate.dataSourceId")
        i-switch(v-model="filterTemplate.havaData")
        | &nbsp; 如果开启，那么复制的内容就包含数据，否则就只有表头。
      FormItem(label="备注" prop="note")
        Input(v-model="filterTemplate.note" type="textarea" :autosize="{minRows: 2,maxRows: 5}")
  Modal(v-model="showSortAdditionalDataFilterModel" title="时间段排序" @on-ok="sortAdditionalDataFilterHander" :loading="showSortAdditionalDataFilterModelLoading")
    .coptions-table
      Row.coptions-title
        Col(span="10")
          | 顺序
        Col(span="14")
          | 时间段
      draggable(v-model="additionalDataFiliterList" :options="{handle: '.index', dragClass: 'dragging'}" @start="dragging = true" @end="dragging = false")
        Row(class="coptions-item" v-for="(item, index) in additionalDataFiliterList" :key="index" :gutter="10")
          Col(span="10" class="index")
            | {{ parseInt(index) + 1 }}
          Col(span="14" class="index")
            | {{ item.name }}
  Modal(v-model="showSortColModel" title="列排序" @on-ok="sortColHander" :loading="showSortColLoading")
    .coptions-table
      Row.coptions-title
        Col(span="10")
          | 顺序
        Col(span="14")
          | 时间段
      draggable(v-model="additionalDataTypes" :options="{handle: '.index', dragClass: 'dragging'}" @start="draggingCol = true" @end="draggingCol = false")
        Row(class="coptions-item" v-for="(item, index) in additionalDataTypes" :key="index" :gutter="10")
          Col(span="10" class="index")
            | {{ parseInt(index) + 1 }}
          Col(span="14" class="index")
            | {{ item.name }}
  Modal(v-model="showAdditionalDataTypeModel" :title="(colTemplate.isEdit ? '编辑' : '添加新') + '列'" @on-ok="additionalDataTypeHander" :loading="showAdditionalDataTypeModelLoading")
    Form(:label-width="90" ref="colFormValidate" :model="colTemplate" :rules="colValidateRule")
      FormItem(label="名称" prop="showName")
        Input(v-model="colTemplate.showName")
      FormItem(label="自定义代码" prop="name")
        Input(v-model="colTemplate.name")
      FormItem(label="数据类型" prop="inputType")
        Select(v-model="colTemplate.inputType" @on-change="colTemplateTypeChange")
          Option(value="text") 文本
          Option(value="select") 下拉
          Option(value="checkbox") 多选
          Option(value="radio") 单选
          Option(value="datePicker") 日期选择
          Option(value="dateTimePicker") 日期时间选择
          Option(value="yearMonthPicker") 年月选择
          Option(value="yearPicker") 年选择
      FormItem(label="最大长度" v-if="colTemplate.inputType === 'text'" prop="maxLength")
        Input(v-model="colTemplate.maxLength", placeholder="留空为不限")
      FormItem(label="数据模板 *（可拖动序号排序）" v-if="colTemplate.inputType === 'select' || colTemplate.inputType === 'radio' || colTemplate.inputType === 'checkbox'" prop="maxLength")
        .coptions-table
          Row.coptions-title
            Col(span="4")
              | 序号
            Col(span="10")
              | 名称 (留空无效)
            Col(span="10")
              | 值（留空则和名称相同）
          draggable(v-model="colTemplate.listItems" :options="{handle: '.index', dragClass: 'dragging'}" @start="draggingItem = true" @end="draggingItem = false")
            Row(class="coptions-item" v-for="(item, index) in colTemplate.listItems" :key="index" :gutter="10")
              Col(span="4" class="index")
                | {{ parseInt(index) + 1 }}
              Col(span="10")
                Input(v-model="item.text")
              Col(span="10")
                Input(v-model="item.value")
        Button(@click="addListItem(colTemplate.listItems)" style="float:right; margin-right: 10px; margin-top: 10px")
          | 增加一行
      FormItem(label="备注" prop="note")
        Input(v-model="colTemplate.note" type="textarea" :autosize="{minRows: 2,maxRows: 5}")
</template>

<script>
import { getCompontent } from '@/api/component'
import componentItemAdditionalData from '@/api/componentItemAdditionalData'
// import JsTreeList from 'js-tree-list'
import { toTreeData } from '@/libs/util'
import draggable from 'vuedraggable'

export default {
  name: 'tree-grid',
  components: {
    draggable
  },
  data () {
    return {
      loading: true,
      TableAreaHeight: document.body.clientHeight - 160,
      additionalDataFiliterList: [],
      componentInfo: null,
      nowEditId: null,
      columns: [
        {
          title: '名称',
          name: 'name',
          width: 400
        },
        {
          title: 'Name1',
          name: 'name1'
        },
        {
          title: 'Name2',
          name: 'name2'
        },
        {
          title: 'Name3',
          name: 'name3'
        },
        {
          title: '备注',
          name: 'remarks'
        }
      ],
      columnsTemplate: [
        {
          title: '名称',
          name: 'name',
          width: 400
        },
        {
          title: 'Name1',
          name: 'name1'
        },
        {
          title: 'Name2',
          name: 'name2'
        },
        {
          title: 'Name3',
          name: 'name3'
        },
        {
          title: '备注',
          name: 'remarks'
        }
      ],
      cantDeleteColumns: ['name', 'name1', 'name2', 'name3', 'remarks'],
      colTemplate: {
        id: 0,
        name: '',
        sort: 0,
        type: 1,
        inputType: 'text',
        dataFilterId: null,
        otherData: null,
        maxLength: null,
        listItems: [
          { text: '', value: '' },
          { text: '', value: '' },
          { text: '', value: '' },
          { text: '', value: '' }
        ],
        note: '',
        showName: '',
        isEdit: false
      },
      filterTemplate: {
        componentId: 0,
        endTime: '2018-09-10T06:35:17.835Z',
        id: 0,
        name: '',
        sort: 0,
        startTime: '2018-09-10T06:35:17.835Z',
        times: [],
        type: '1',
        dataSourceId: null,
        havaData: false,
        note: '',
        showName: '',
        isEdit: false
      },
      filterValidateRule: {
        name: [
          { required: true, message: '自定义代码不能为空', trigger: 'blur' }
        ],
        showName: [
          { required: true, message: '名称不能为空', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择有效期类型', trigger: 'change' }
        ]
      },
      colValidateRule: {
        name: [
          { required: true, message: '自定义代码不能为空', trigger: 'blur' }
        ],
        showName: [
          { required: true, message: '名称不能为空', trigger: 'blur' }
        ],
        inputType: [
          { required: true, message: '请选择数据类型', trigger: 'change' }
        ]
      },
      grid: {},
      gridData: [],
      showAdditionalDataFilterModel: false,
      showAdditionalDataFilterModelLoading: true,
      gridComponent: null,
      loadingData: true,
      listAdditionalData: [],
      additionalDataTypes: [],
      additionalDatas: [],
      showSortAdditionalDataFilterModel: false,
      showSortAdditionalDataFilterModelLoading: true,
      dragging: false,
      showAdditionalDataTypeModel: false,
      showAdditionalDataTypeModelLoading: true,
      showSortColModel: false,
      showSortColLoading: true,
      draggingCol: false,
      type: new Map([
        [1000, 'text'],
        ['text', 1000],
        [1001, 'select'],
        ['select', 1001],
        [19000, 'checkbox'],
        ['checkbox', 19000],
        [1002, 'radio'],
        ['radio', 1002],
        [2000, 'datePicker'],
        ['datePicker', 2000],
        [2005, 'dateTimePicker'],
        ['dateTimePicker', 2005],
        ['yearMonthPicker', 2001],
        [2001, 'yearMonthPicker'],
        ['yearPicker', 2002],
        [2002, 'yearPicker']
      ]),
      draggingItem: false
    }
  },
  watch: {
    nowEditId (newVal, oldVal) {
      console.log('当前正在操作数据的id', newVal)
      console.log('当前正在操作数据的id', this.nowEditId)
      this.tabChange(oldVal)
    }
  },
  async mounted () {
    await this.dataInit()
    this.$nextTick(function () {
      if (this.additionalDataFiliterList && this.additionalDataFiliterList.length) {
        this.CreateGrid(this.nowEditId, this.gridData, this.additionalDatas)
      }
      console.log(this.grid)
      this.loading = false
    })
  },
  methods: {
    addListItem (list) {
      list.push({ text: '', value: '' })
    },
    async sortColHander () {
      this.showSortColLoading = true
      for (const n in this.additionalDataTypes) {
        this.additionalDataTypes[n].sort = n
        const r = await componentItemAdditionalData.saveAddtionalDataType(this.additionalDataTypes[n])
        if (r && r.response.code === 0) {
          this.$Message.success(this.additionalDataTypes[n].name + '的数据设置成功')
        } else if (r) {
          this.$Message.error(this.additionalDataTypes[n].name + '数据出错：' + r.response.msg || this.additionalDataTypes[n].name + '的数据保存失败，请稍后重试')
        } else {
          this.$Message.error(this.additionalDataTypes[n].name + '数据出错：网络错误，请稍后重试')
        }
      }
      this.$Message.success('排序设置成功')
      this.showSortColModel = false
      this.filterTemplateInit()
      this.dataInit(2)
      this.showSortColLoading = false
    },
    handleColRemove () {
      const tmp = this.grid.getFocusedCell()
      console.log(tmp)
      if (!tmp.columnName || this.cantDeleteColumns.includes(tmp.columnName)) {
        this.$Modal.info({
          title: '抱歉',
          content: '<p>当前选择的列不允许删除</p>'
        })
      } else {
        this.$Modal.confirm({
          title: '警告',
          content: '<p>删除后无法恢复，确定删除该列及其数据吗？</p>',
          onOk: async () => {
            const r = await componentItemAdditionalData.removeAdditionalDataType(tmp.columnName)
            if (r && r.response.code === 0) {
              this.$Message.success('删除成功')
              this.dataInit(2)
            } else if (r) {
              this.$Message.error(r.response.msg || '删除失败，请重试')
            } else {
              this.$Message.error('网络错误，请稍后重试')
            }
          }
        })
      }
    },
    getType (inputType) {
      // 1000 普通文本   inputType ↓↓↓
      // 2000 日期
      // 3000 金额
      // 4000 数字
      // 4001 小数
      // 4002 正数
      // 4303 正整数
      // 5000 时间
      // 6000 手机
      // 7000 固话
      // 8000 邮件
      // 9000 密码
      // 1000 网址
      // 11000 身份证
      // 12000 仅汉字
      // 13000 英文
      // 14000 人员
      // 15000 区域
      // 16000 文件
      // 17000 年龄 （返回值是负数：-1为“一个月大的婴儿”，-11为“十一个月大的婴儿”）
      // 18000 小报告 （小报告没有返回值，但是inputType不能为空）
      // 1001 下拉
      // 19000 多选 (以英文逗号分割的字符串)
      // 1002 单选
      // 2005 日期时间选择
      // 2001 年月选择
      // 2002 年选择
      // Option(value="text") 文本
      // Option(value="select") 下拉
      // Option(value="checkbox") 多选
      // Option(value="radio") 单选
      // Option(value="datePicker") 日期选择
      // Option(value="dateTimePicker") 日期时间选择
      // Option(value="yearMonthPicker") 年月选择
      // Option(value="yearPicker") 年选择
      return this.type.get(inputType)
    },
    getInputType (type) {
      return this.type.get(type)
    },
    colTemplateTypeChange (val) { // TODO

    },
    async additionalDataTypeHander () {
      this.showAdditionalDataTypeModelLoading = true
      this.$refs['colFormValidate'].validate(async (valid) => {
        if (valid) {
          this.colTemplate.componentId = this.$route.query.id
          if (this.additionalDataTypes && this.additionalDataTypes.length) {
            this.colTemplate.sort = this.additionalDataTypes.length + 1
          } else {
            this.colTemplate.sort = 1
          }
          this.colTemplate.type = this.getType(this.colTemplate.inputType)
          const otherData = {
            inputType: this.colTemplate.inputType,
            maxLength: this.colTemplate.maxLength,
            listItems: this.colTemplate.listItems
          }
          this.colTemplate.otherData = JSON.stringify(otherData)
          const r = await componentItemAdditionalData.saveAddtionalDataType(this.colTemplate)
          if (r && r.response.code === 0) {
            this.$Message.success('列设置成功')
            this.showAdditionalDataTypeModel = false
            this.colTemplateInit(this.colTemplate.dataFilterId)
            this.dataInit(2)
          } else if (r) {
            this.$Message.error(r.response.msg || '保存失败，请检查输入数据是否正确')
          } else {
            this.$Message.error('网络错误，请稍后重试')
          }
        } else {
          this.$Message.error('请检查必填项!')
        }
        this.showAdditionalDataTypeModelLoading = false
      })
    },
    async sortAdditionalDataFilterHander () {
      this.showAdditionalDataFilterModelLoading = true
      for (const n in this.additionalDataFiliterList) {
        this.additionalDataFiliterList[n].sort = n
        const r = await componentItemAdditionalData.saveAdditionalDataFilter(this.additionalDataFiliterList[n])
        if (r && r.response.code === 0) {
          this.$Message.success(this.additionalDataFiliterList[n].name + '的数据设置成功')
        } else if (r) {
          this.$Message.error(this.additionalDataFiliterList[n].name + '数据出错：' + r.response.msg || this.additionalDataFiliterList[n].name + '的数据保存失败，请稍后重试')
        } else {
          this.$Message.error(this.additionalDataFiliterList[n].name + '数据出错：网络错误，请稍后重试')
        }
      }
      this.$Message.success('排序设置成功')
      this.showSortAdditionalDataFilterModel = false
      this.filterTemplateInit()
      this.dataInit(2)
      this.showAdditionalDataFilterModelLoading = false
    },
    async getListAdditionalData (additionalDataFilterId) {
      const data = await componentItemAdditionalData.listAdditionalData(additionalDataFilterId)
      this.listAdditionalData = data || null
      this.additionalDataTypes = (data && data.additionalDataTypes) || []
      if (data && data.additionalDataMap && data.additionalDataMap.additionalData && data.additionalDataMap.additionalData.length) {
        this.additionalDatas = data.additionalDataMap.additionalData
      } else {
        this.additionalDatas = []
      }

      this.additionalDataTypes.sort((a, b) => {
        return a.sort - b.sort
      })
    },
    async dataInit (type = 1) { // 1初始化，2更新初始化。配置修改的时候，组件数据是不变的，没有必要再次请求组件数据。
      let tmp = null
      if (type === 1) {
        tmp = await Promise.all([componentItemAdditionalData.listAdditionalDataFiliter(this.$route.query.id), getCompontent(this.$route.query.id)])
        tmp[0].sort((a, b) => {
          return a.sort - b.sort
        })
        this.additionalDataFiliterList = tmp[0]
        this.componentInfo = tmp[1]
        const tmp1 = this.componentInfo.items
        this.gridComponent = tmp1.shift()
        console.log(this.gridComponent)
        // this.gridData = new JsTreeList.ListToTree(tmp1, {
        //   key_id: 'dataUuid',
        //   key_parent: 'parentUuid',
        //   key_child: '_children',
        //   key_last: null,
        //   uuid: true,
        //   empty_children: true
        // }).GetTree()
        this.gridData = toTreeData(tmp1, 'null', 'dataUuid', 'parentUuid', '_children')
        if (this.additionalDataFiliterList && this.additionalDataFiliterList.length) {
          // 默认选中第一个
          this.nowEditId = this.additionalDataFiliterList[0].id

          await this.getListAdditionalData(this.nowEditId)
        } else {
          this.loading = false
        }
      } else {
        tmp = await Promise.all([componentItemAdditionalData.listAdditionalDataFiliter(this.$route.query.id)])
        this.additionalDataFiliterList = tmp[0]

        if (this.additionalDataFiliterList && this.additionalDataFiliterList.length) {
          await this.getListAdditionalData(this.nowEditId)

          if (!this.nowEditId || !this.findInList()) {
            this.nowEditId = this.additionalDataFiliterList[0].id
          }

          this.CreateGrid(this.nowEditId, this.gridData, this.additionalDatas)
        }

        this.loading = false
      }
    },
    findInList () {
      for (const n in this.additionalDataFiliterList) {
        if (this.additionalDataFiliterList[n].id === this.nowEditId) {
          return true
        }
      }
      return false
    },
    getDateTime (dateTime) {
      return this.$moment(dateTime).format('YYYY-MM-DD HH:mm:ss')
    },
    async tabChange (oldId) {
      if (oldId) {
        this.loading = true
        await this.save(oldId, false)
        this.dataInit(2)
      }
    },
    async save (oldId, message) {
      if (this.grid && this.grid.isModified) {
        const nowFiliter = this.additionalDataFiliterList.find(val => {
          if (oldId) {
            return val.id === oldId
          }
          return val.id === this.nowEditId
        })
        if (this.grid.isModified()) {
          if (message) this.$Message.info('正在保存中...')
          // const tmp = this.grid.getRows()
          const tmp = this.grid.getModifiedRows().updatedRows // 只保存被修改的数据
          // console.log(this.grid.getModifiedRows())
          const additionalData = []
          for (const n in tmp) { // 过滤多余数据
            const tmp1 = {}
            for (const i in tmp[n]) {
              if (i === 'id') {
                tmp1['componentItemId'] = tmp[n][i]
              } else {
                const item = this.additionalDataTypes.find(val => {
                  return val.id === i
                })
                if (item) {
                  tmp1[i] = tmp[n][i]
                }
              }
            }
            if (Object.keys(tmp1).length > 1) additionalData.push(tmp1)
          }
          const r = await componentItemAdditionalData.saveAdditionalData({
            additionalData,
            additionalDataFilterId: nowFiliter.id,
            componentId: this.$route.query.id
          })
          if (r) {
            if (message) {
              this.$Message.success('保存成功')
            } else {
              this.$Message.success('自动保存“' + nowFiliter.name + '”的数据成功')
            }
          } else {
            if (message) {
              this.$Message.error('保存过程中发生了错误，请稍后再试')
            } else {
              this.$Message.error('自动保存“' + nowFiliter.name + '”数据的过程中发生了错误，请稍后再试')
            }
          }
        } else {
          if (message) this.$Message.success('没有检测到被修改的数据')
        }
      // } else { // 全部保存
      //   const tmp = []
      //   for (const n in this.additionalDataFiliterList) {
      //     tmp.push(this.save(this.additionalDataFiliterList[n].id))
      //   }
      //   this.$Message.info('正在保存中...')
      //   const r = await Promise.all(tmp)
      //   if (r.length === tmp.length) {
      //     this.$Message.success('全部保存完成')
      //   } else {
      //     this.$Message.error('全部保存过程中发生了错误，请稍后再试')
      //   }
      }
    },
    filterTemplateType (val) {
      if (val == 2) this.filterTemplate.endTime = '' // eslint-disable-line
    },
    filterTemplateDateLongTime (val) {
      this.filterTemplate.startTime = val
      this.filterTemplate.endTime = ''
    },
    filterTemplateDate (val) {
      console.log(val)
      this.filterTemplate.startTime = val[0]
      this.filterTemplate.endTime = val[1]
    },
    filterTemplateInit (isEdit = false) {
      this.$set(this, 'filterTemplate', {
        componentId: 0,
        endTime: '2018-09-10T06:35:17.835Z',
        id: 0,
        name: '',
        sort: 0,
        startTime: '2018-09-10T06:35:17.835Z',
        times: [],
        type: '1',
        dataSourceId: null,
        havaData: false,
        note: '',
        showName: '',
        isEdit
      })
    },
    colTemplateInit (dataFilterId, isEdit = false) {
      this.$set(this, 'colTemplate', {
        id: 0,
        name: '',
        sort: 0,
        type: 1,
        inputType: 'text',
        dataFilterId,
        otherData: null,
        maxLength: null,
        note: '',
        showName: '',
        listItems: [
          { text: '', value: '' },
          { text: '', value: '' },
          { text: '', value: '' },
          { text: '', value: '' }
        ],
        isEdit
      })
    },
    DataHandler (gridData, additionalDatas) {
      for (const i in gridData) {
        for (const n in additionalDatas) {
          if (additionalDatas[n].componentItemId === gridData[i].id) {
            Object.assign(gridData[i], additionalDatas[n])
          }
        }

        // for (const x in this.additionalDataTypes) {
        //   if (!gridData[i].hasOwnProperty(this.additionalDataTypes[x].id)) {
        //     gridData[i][this.additionalDataTypes[x].id] = ''
        //   }
        // }

        if (gridData[i]._children && gridData[i]._children.length) {
          this.DataHandler(gridData[i]._children, additionalDatas)
        }
      }
    },
    getColType (type) {
      const tmp = this.getInputType(type)
      if (tmp === 'datePicker' || tmp === 'dateTimePicker' || tmp === 'yearMonthPicker' || tmp === 'yearPicker') {
        return 'text'
      } else {
        return tmp
      }
    },
    CreateGrid (id, gridData, additionalDatas) {
      if ($('#grid')) {
        $('#grid').html('')
        console.log(gridData)
        console.time('数据处理耗时')
        gridData = this.deepCopy(gridData)
        this.DataHandler(gridData, additionalDatas)
        console.timeEnd('数据处理耗时')
        // console.log(gridData)
        const xGridData = gridData
        this.columns = this.deepCopy(this.columnsTemplate) // 先置空，再新增
        for (const n in this.additionalDataTypes) { // TODO: 添加类型
          const otherData = JSON.parse(this.additionalDataTypes[n].otherData)
          const tmp = {
            title: this.additionalDataTypes[n].showName + '(' + this.additionalDataTypes[n].name + ')',
            name: this.additionalDataTypes[n].id,
            editOptions: {
              type: this.getColType(this.additionalDataTypes[n].type),
              // maxLength: 10,
              useViewMode: true
            }
          }

          if (tmp['editOptions']['type'] === 'select' || tmp['editOptions']['type'] === 'checkbox' || tmp['editOptions']['type'] === 'radio') {
            if (otherData && otherData.listItems) {
              const template = this.deepCopy(otherData.listItems)
              for (let n = template.length - 1; n >= 0; n--) {
                if (template[n].text === null || template[n].text === '') {
                  template.splice(n, 1)
                } else {
                  if (template[n].value === null || template[n].value === '') {
                    template[n].value = template[n].text
                  }
                }
              }

              tmp['editOptions']['listItems'] = template
            } else {
              tmp['editOptions']['listItems'] = []
            }
          }

          if (otherData && otherData.maxLength && tmp['editOptions']['type'] === 'text') {
            tmp['editOptions']['maxLength'] = otherData.maxLength
          }

          const comType = this.getInputType(this.additionalDataTypes[n].type)
          if (comType === 'datePicker' || comType === 'dateTimePicker' || comType === 'yearMonthPicker' || comType === 'yearPicker') {
            tmp.component = {
              name: 'datePicker',
              options: {}
            }

            if (comType === 'datePicker') {
              tmp.component.options.format = 'yyyy-MM-dd'
              tmp.component.options.date = new Date()
            } else if (comType === 'dateTimePicker') {
              tmp.component.options.format = 'yyyy-MM-dd HH:mm:ss'
              tmp.component.options.timepicker = true
            } else if (comType === 'yearMonthPicker') {
              tmp.component.options.format = 'yyyy-MM'
              tmp.component.options.type = 'month'
            } else {
              tmp.component.options.format = 'yyyy'
              tmp.component.options.type = 'year'
            }
          }

          console.log(this.additionalDataTypes)
          console.log(tmp)

          this.columns.push(tmp)
        }
        if (this.grid && this.grid.destroy) this.grid.destroy()
        this.grid = null
        // console.log(this.columns)
        this.grid = new tui.Grid({
          el: $('#grid'),
          data: xGridData,
          rowHeaders: [{
            type: 'rowNum',
            title: '顺序'
          }],
          bodyHeight: this.TableAreaHeight - 160,
          rowHeight: 40,
          virtualScrolling: true,
          treeColumnOptions: {
            name: 'name',
            useCascadingCheckbox: false
          },
          columns: this.columns
        })
      } else {
        console.error('元素不存在')
        this.$Message.error('发生致命错误，请刷新重试或联系管理员')
      }
      this.loadingData = false
    },
    addCol (dataFilterId) {
      this.colTemplateInit(dataFilterId)
      this.showAdditionalDataTypeModel = true
      // this.columns.push({
      //   title: '新数据',
      //   name: 'new',
      //   editOptions: {
      //     type: 'text',
      //     useViewMode: true
      //   }
      // })
      // this.grid[id].setColumns(this.columns)
    },
    editCol () {
      const tmp = this.grid.getFocusedCell()
      console.log(tmp)
      if (!tmp.columnName || this.cantDeleteColumns.includes(tmp.columnName)) {
        this.$Modal.info({
          title: '抱歉',
          content: '<p>当前选择的列不允许修改</p>'
        })
      } else {
        const item = this.additionalDataTypes.find(val => {
          return val.id === tmp.columnName
        })

        const template = {
          id: item.id,
          name: item.name,
          sort: item.sort,
          type: item.type,
          inputType: this.getInputType(item.type),
          dataFilterId: item.dataFilterId,
          otherData: item.otherData,
          note: item.note,
          showName: item.showName,
          isEdit: true
        }

        const otherData = JSON.parse(item.otherData) // 处理后端没有直接保存的数据

        if (otherData.listItems) {
          template['listItems'] = otherData.listItems
          let sum = 0
          for (const n in template['listItems']) {
            if (template['listItems'][n].text === '') {
              sum++
            }
          }

          // const time = sum - 4

          // console.log(time)

          // if (time > 0) {
          //   for (let n = 0; n < time; n++) {
          //     for (const n in template['listItems']) {
          //       console.log(template['listItems'][n])
          //       if (template['listItems'][n] !== null && template['listItems'][n].text === '') {
          //         template['listItems'].splice(n, 1)
          //         --sum
          //         console.log('删除一个', sum)
          //         break
          //       }
          //     }
          //   }
          // }

          while (sum > 4) {
            for (const n in template['listItems']) {
              console.log(template['listItems'][n])
              if (template['listItems'][n] !== null && template['listItems'][n].text === '') {
                template['listItems'].splice(n, 1)
                --sum
                console.log('删除一个', sum)
                break
              }
            }
          }

          // console.log(template.listItems)
        } else {
          template['listItems'] = [
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' }
          ]
        }

        if (otherData.maxLength) {
          template['maxLength'] = otherData.maxLength
        } else {
          template['maxLength'] = null
        }

        this.$set(this, 'colTemplate', template)

        this.showAdditionalDataTypeModel = true
      }
    },
    additionalDataFilterHander () {
      this.showAdditionalDataFilterModelLoading = true
      this.$refs['filterFormValidate'].validate(async (valid) => {
        if (valid) {
          this.filterTemplate.componentId = this.$route.query.id
          if (this.additionalDataFiliterList && this.additionalDataFiliterList.length) {
            this.filterTemplate.sort = this.additionalDataFiliterList.length + 1
          } else {
            this.filterTemplate.sort = 1
          }
          const r = await componentItemAdditionalData.saveAdditionalDataFilter(this.filterTemplate)
          if (r && r.response.code === 0) {
            this.$Message.success('时间段设置成功')
            this.showAdditionalDataFilterModel = false
            this.filterTemplateInit()
            this.dataInit(2)
          } else if (r) {
            this.$Message.error(r.response.msg || '保存失败，请检查输入数据是否正确')
          } else {
            this.$Message.error('网络错误，请稍后重试')
          }
        } else {
          this.$Message.error('请检查必填项!')
        }
        this.showAdditionalDataFilterModelLoading = false
      })
    },
    handleTabRemove (id) {
      this.$Modal.confirm({
        title: '警告',
        content: '<p>删除后无法恢复，确定删除该时段数据吗？</p>',
        onOk: async () => {
          const r = await componentItemAdditionalData.removeAdditionalDataFiliter(id)
          if (r && r.response.code === 0) {
            this.$Message.success('删除成功')
            this.loading = true
            this.dataInit(2)
          } else if (r) {
            this.$Message.error(r.response.msg || '删除失败，请重试')
          } else {
            this.$Message.error('网络错误，请稍后重试')
          }
        }
      })
    },
    addAdditionalDataFilter () {
      this.filterTemplateInit()
      this.showAdditionalDataFilterModel = true
    },
    updateAdditionalDataFilter (id) {
      const item = this.additionalDataFiliterList.find(val => {
        return val.id === id
      })

      this.$set(this, 'filterTemplate', {
        componentId: item.componentId,
        endTime: item.endTime,
        id: item.id,
        name: item.name,
        sort: item.sort,
        startTime: item.startTime,
        times: [this.getDateTime(item.startTime), this.getDateTime(item.endTime)],
        type: item.type,
        havaData: false,
        dataSourceId: null,
        note: item.note,
        showName: item.showName,
        isEdit: true
      })

      this.showAdditionalDataFilterModel = true
    },
    sort () {
      this.showSortAdditionalDataFilterModel = true
    },
    sortCol () {
      this.showSortColModel = true
    }
  }
}
</script>

